body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Slab', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.background {
  background-image: #FDFDFD;
  width: 100vw;
  height: 100vh;
}

.information-container {
  min-width: 720px;
  width: 33%;
  color: #444444;
  margin: auto;
  padding: 100px 0;
}

t1 {
  font-size: 56px;
}

t3 {
  font-size: 30px;
}

t4 {
  font-size: 22px;
}

p {
  font-size: 16px;
}

.section-container {
  padding-top: 40px;
  padding-bottom: 40px;
}

.section-title {
  margin-bottom: 30px;
}

.header {
  font-size: 20px;
  text-align: center;
}

.brief {
  padding-top: 25px;
}

.img-container {
  width: 140px;
  height: 140px;
  margin: 0;
  background-color: #444444;
  border-radius: 50%;
  padding: 2px;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  overflow: hidden;
}

/* resize images */
.img-container img {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  margin: 0;
}

.about-me-container {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

p {
  margin: 0;
}

hr {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin: auto 0;
  height: 2px;
  background-color: #444444;
}

.personal-photo-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.about-me-flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.social-links-container {
  margin-top: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: right;
          justify-content: right;
  -webkit-align-content: center;
          align-content: center;
}

.social-link {
  margin: 10px 0;
  height: 30px;
  width: 30px;
}

img {
  width: 30px;
  height: 30px;
}

svg {
  fill: #444444;
}

.section-entry {
  display: -webkit-flex;
  display: flex;
  margin-top: 40px;
}

.entry-description {
  margin-left: 20px;
}

.entry-brief-container {
  -webkit-flex-direction: column;
          flex-direction: column;
  width: auto;
  white-space: nowrap;
  min-width: 215px;
}

.entry-title {
  text-align: center;
  margin: 0 auto auto;
}

.entry-info {
  margin-top: 10px;
}

.entry-info p {
  font-size: 18px;
}

a {
  color: #0080FF;
  text-decoration: none;
  font-size: 18px;
}


p {
  margin-top: 0;
  padding-top: 5px;
}

